.site
{
	main.post
	{
		background-color: #DCC1A7;
		background-image: url('images/single-background.min.jpg');
		background-position: top center;
		background-repeat: repeat;
		background-size: 20em;
		color: darken(#A88C76, 40%);
		font-weight: 300;
		min-height: 100%;
		overflow-x: hidden;
		padding-bottom: 4em;
		padding-top: 5.5em;
		width: 100%;

		hr
		{
			border-style: dashed;
			border-width: 1px 0 0 0;
			border-color: black;
			padding: 0;
			margin: 2em 1em;
		}

		iframe
		{
			border: none;
			border-radius: 0.25em;
			display: block;
			margin: 0 auto;
			max-width: 90%;
		}

		.post-header
		{
			.post-title
			{
				font-size: 2em;
				margin-bottom: 0.5em;
				margin-top: 1em;
			}

			.post-meta
			{
				line-height: 1.5em;
				margin-bottom: 1em;

				.post-author
				{
					font-style: normal;
				}

				img
				{
					border-color: lighten( $urbanrest-black, 33% );
					border-radius: 50%;
					border-style: solid;
					border-width: 2pt;
					box-sizing: border-box;
					float: left;
					height: 3em;
					margin-right: 0.75em;
					width: auto;
				}

				.category
				{
					float: right;
					margin-top: -3em;
					text-align: right;
				}
			}
		}

		.post-image
		{
			align-items: center;
			background-position: center;
			@include background-size( cover );
			border-radius: 0.25em;
			display: flex;
			height: 0;
			justify-content: center;
			margin: 2em 0 0;
			overflow: hidden;
			padding-bottom: 25%;
			padding-top: 25%;
			position: relative;
			width: 100%;

			img
			{
				display: none;
				height: auto;
				width: 100%;
			}

			figcaption
			{
				background-color: rgba( black, 0.55 );
				bottom: 0;
				box-sizing: border-box;
				color: white;
				font-weight: 400;
				left: 0;
				max-height: 0;
				opacity: 0;
				padding: 0 1em;
				position: absolute;
				@include transition( max-height 0.5s, opacity 0.33s, padding-bottom 0.5s, padding-top 0.5s );
				width: 100%;

				.post-image-title
				{
					font-size: 1.5em;
				}

				.post-image-caption
				{
					font-size: 0.8em;
					font-style: italic;
					font-weight: 100;
				}

				.post-image-description
				{
					font-size: 1em;
					margin-top: 0.5em;
				}
			}

			&:hover 
			{
				figcaption
				{
					max-height: 100%;
					opacity: 1;
					padding: 1em 1em;
				}
			}
		}

		.post-content
		{
			font-size: 1.2em;
			line-height: 1.5em;

			h2
			{
				font-size: 1.3em;
				margin-bottom: 0.25em;
			}

			h4
			{
				margin-bottom: 0.25em;
				margin-top: 0.5em;
			}

			p, table
			{
				margin-bottom: 1em;
			}

			ul
			{
				list-style: disc;
				padding-left: 1.5em;
			}

			section
			{
				margin-top: 1em;
			}

			a
			{
				@extend .hyperlink;
			}
		}

		.post-tags
		{
			font-size: 1.2em;
			line-height: 1.5em;
			margin-top: 2em;

			ul
			{
				list-style: none;
				padding: 0;
			}

			.post-tag
			{
				display: inline;
				font-size: 0.8em;

				a
				{
					background-color: rgba( $urbanrest-black, 0.3 );
					border: none;
					border-radius: 0.25em;
					color: inherit;
					display: inline-block;
					margin-bottom: 0.5em;
					margin-right: 0.5em;
					padding: 0.25em 0.75em;
					@include transition( background-color 0.33s );

					&:hover
					{
						background-color: rgba( $urbanrest-green, 0.3 );
						color: $urbanrest-green;
						border: none;
						@include transition( background-color 0.11s );
					}
				}

				&:last-child a
				{
					margin-right: 0;
				}
			}
		}

		.post-footer
		{
			margin-top: 3em;

			.post-sharing.modal:not(.modal-opened):not(.modal-closing) { display: none; }

			.post-actions
			{
				text-align: center;

				.post-share
				{
					@extend .share;
				}
			}

			.post-tags
			{
				list-style: none;
				margin: 3em 0;
				padding: 0;

				li
				{
					display: inline;

					a
					{
						background-color: rgba( $urbanrest-black, 0.3 );
						border: none;
						border-radius: 0.25em;
						color: inherit;
						display: inline-block;
						margin-right: 0.5em;
						padding: 0.5em;
						@include transition( background-color 0.33s );

						&:hover
						{
							background-color: rgba( $urbanrest-green, 0.3 );
							color: $urbanrest-green;
							border: none;
							@include transition( background-color 0.11s );
						}
					}

					&:last-child a
					{
						margin-right: 0;
					}
				}
			}
		}
	}
}